/* App Component Style------------------------------------------------------------ */
body{
background-image: linear-gradient(to right, #f7bbbe, #e7ecac);
}
.heading {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  text-decoration: underline;
  text-align: center;
  color: rgb(248, 65, 37);

}
.app {

  display: flex;
  margin-top: 8%;
  justify-content: center;
  
}

/* end app style------------------------------------------------------------------ */



/* table style-------------------------------------------------------------------- */

/* table row */
.row {
  background-color: blueviolet;
}

/* table data */
td {
  background-color: #f6cbc7;
  text-align: center;
  padding: 5px;
  color: white;
  border-right: 1px solid black;
}
.none {
  background-color:#f7b5af;
  color: black;
}
/* end table style---------------------------------------------------------------- */



/* tooltips style----------------------------------------------------------------- */

